<script setup lang="ts">
import { ref, watch } from 'vue'
import { ElMessage } from 'element-plus'
import { getOrderDetail } from '../../api/orderManagement.ts'

const visible = ref(false)
const loading = ref(false)
const logsLoading = ref(false)

// 订单详情数据
const orderDetail = ref({
  id: 0,
  order_no: '',
  merchant_order_no: '',
  third_party_order_no: '',
  trace_id: '',
  merchant_id: '',
  product_id: '',
  channel_id: '',
  amount: 0,
  fee: 0,
  status: 1,
  payment_method: '',
  notify_url: '',
  return_url: '',
  client_ip: '',
  user_agent: '',
  subject: '',
  body: '',
  extra_data: {},
  third_party_response: {},
  notify_count: 0,
  notify_status: 0,
  expire_time: '',
  paid_time: '',
  created_at: '',
  updated_at: '',
  product: null as any,
  channel: null as any
})

// 订单流转日志数据
const orderLogs = ref([])

// 订单状态选项
const statusOptions = [
  { label: '待支付', value: 1 },
  { label: '支付中', value: 2 },
  { label: '支付成功', value: 3 },
  { label: '支付失败', value: 4 },
  { label: '已退款', value: 5 },
  { label: '已关闭', value: 6 }
]

// 通知状态选项
const notifyStatusOptions = [
  { label: '未通知', value: 0 },
  { label: '通知成功', value: 1 },
  { label: '通知失败', value: 2 }
]

// 操作者类型选项
const operatorTypeOptions = [
  { label: '系统', value: 'system' },
  { label: '管理员', value: 'admin' },
  { label: '商户', value: 'merchant' }
]

// 打开弹窗
const open = async (data: any = {}) => {
  if (data.id) {
    loading.value = true
    try {
      // 获取订单详情
      const res = await getOrderDetail(`/api/v1/admin/order/${data.id}`)
      console.log('订单详情响应:', res)
      
      // 处理响应格式 - request工具返回的是完整的响应对象
      let orderData = null
      if (res && res.data) {
        orderData = res.data
      }
      
      if (orderData) {
        Object.assign(orderDetail.value, orderData)
        // 转换金额显示（分转元）
        if (orderData.amount) {
          orderDetail.value.amount = orderData.amount / 100
        }
        if (orderData.fee) {
          orderDetail.value.fee = orderData.fee / 100
        }
      } else {
        ElMessage.error('获取订单详情失败：数据格式错误')
        return
      }
      
      // 获取订单流转日志
      await loadOrderLogs(data.id)
    } catch (error: any) {
      ElMessage.error('获取订单详情失败: ' + (error?.message || error))
      return
    } finally {
      loading.value = false
    }
  }
  visible.value = true
}

// 加载订单流转日志
const loadOrderLogs = async (orderId: number) => {
  logsLoading.value = true
  try {
    const response = await fetch(`/api/v1/admin/order/logs?order_id=${orderId}`)
    const result = await response.json()
    console.log('订单日志响应:', result)
    
    if (result.code === 200) {
      orderLogs.value = result.data || []
    } else {
      console.warn('获取日志失败:', result.msg)
      // 不显示错误消息，因为日志可能为空是正常的
      orderLogs.value = []
    }
  } catch (error) {
    console.error('获取日志失败:', error)
    // 不显示错误消息，因为日志可能为空是正常的
    orderLogs.value = []
  } finally {
    logsLoading.value = false
  }
}

// 获取状态文本
const getStatusText = (status: number) => {
  const option = statusOptions.find(item => item.value === status)
  return option ? option.label : '未知状态'
}

// 获取通知状态文本
const getNotifyStatusText = (status: number) => {
  const option = notifyStatusOptions.find(item => item.value === status)
  return option ? option.label : '未知状态'
}

// 获取操作者类型文本
const getOperatorTypeText = (type: string) => {
  const option = operatorTypeOptions.find(item => item.value === type)
  return option ? option.label : type
}

// 获取状态标签类型
const getStatusTagType = (status: number) => {
  const typeMap: { [key: number]: string } = {
    1: 'warning',
    2: 'primary', 
    3: 'success',
    4: 'danger',
    5: 'info',
    6: 'info'
  }
  return typeMap[status] || 'info'
}

// 获取通知状态标签类型
const getNotifyStatusTagType = (status: number) => {
  const typeMap: { [key: number]: string } = {
    0: 'warning',
    1: 'success',
    2: 'danger'
  }
  return typeMap[status] || 'info'
}

// 重置数据
const resetData = () => {
  orderDetail.value = {
    id: 0,
    order_no: '',
    merchant_order_no: '',
    third_party_order_no: '',
    trace_id: '',
    merchant_id: '',
    product_id: '',
    channel_id: '',
    amount: 0,
    fee: 0,
    status: 1,
    payment_method: '',
    notify_url: '',
    return_url: '',
    client_ip: '',
    user_agent: '',
    subject: '',
    body: '',
    extra_data: {},
    third_party_response: {},
    notify_count: 0,
    notify_status: 0,
    expire_time: '',
    paid_time: '',
    created_at: '',
    updated_at: '',
    product: null as any,
    channel: null as any
  }
  orderLogs.value = []
}

// 关闭弹窗时重置数据
watch(visible, (newVal) => {
  if (!newVal) resetData()
})

defineExpose({ open })
</script>

<template>
  <el-drawer v-model="visible" title="订单详情" size="70%">
    <div v-loading="loading" element-loading-text="加载中...">
      <!-- 基本信息 -->
      <el-divider content-position="left">基本信息</el-divider>
      
      <div class="detail-item order-no-item">
        <label>订单号：</label>
        <span class="order-no">{{ orderDetail.order_no }}</span>
      </div>

      <div class="detail-item">
        <label>商户订单号：</label>
        <span>{{ orderDetail.merchant_order_no }}</span>
      </div>

      <el-row :gutter="20">
        <el-col :span="12">
          <div class="detail-item">
            <label>第三方订单号：</label>
            <span>{{ orderDetail.third_party_order_no || '-' }}</span>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="detail-item">
            <label>追踪ID：</label>
            <span>{{ orderDetail.trace_id || '-' }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <div class="detail-item">
            <label>商户ID：</label>
            <span>{{ orderDetail.merchant_id }}</span>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="detail-item">
            <label>产品ID：</label>
            <span>{{ orderDetail.product_id }}</span>
          </div>
        </el-col>
      </el-row>

      <!-- 金额信息 -->
      <el-divider content-position="left">金额信息</el-divider>
      
      <el-row :gutter="20">
        <el-col :span="12">
          <div class="detail-item">
            <label>订单金额：</label>
            <span class="amount">¥{{ orderDetail.amount.toFixed(2) }}</span>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="detail-item">
            <label>手续费：</label>
            <span class="amount">¥{{ orderDetail.fee.toFixed(2) }}</span>
          </div>
        </el-col>
      </el-row>

      <!-- 状态信息 -->
      <el-divider content-position="left">状态信息</el-divider>
      
      <el-row :gutter="20">
        <el-col :span="12">
          <div class="detail-item">
            <label>订单状态：</label>
            <el-tag :type="getStatusTagType(orderDetail.status) as any">
              {{ getStatusText(orderDetail.status) }}
            </el-tag>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="detail-item">
            <label>通知状态：</label>
            <el-tag :type="getNotifyStatusTagType(orderDetail.notify_status) as any">
              {{ getNotifyStatusText(orderDetail.notify_status) }}
            </el-tag>
          </div>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <div class="detail-item">
            <label>支付产品：</label>
            <span>{{ orderDetail.product?.product_name || '-' }}</span>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="detail-item">
            <label>支付通道：</label>
            <span>{{ orderDetail.channel?.channel_name || '-' }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <div class="detail-item">
            <label>通知次数：</label>
            <span>{{ orderDetail.notify_count }}</span>
          </div>
        </el-col>
      </el-row>

      <!-- 订单内容 -->
      <el-divider content-position="left">订单内容</el-divider>
      
      <div class="detail-item">
        <label>订单描述：</label>
        <span>{{ orderDetail.body || '-' }}</span>
      </div>

      <!-- 回调信息 -->
      <el-divider content-position="left">回调信息</el-divider>
      
      <div class="detail-item">
        <label>通知URL：</label>
        <span class="url-text">{{ orderDetail.notify_url || '-' }}</span>
      </div>

      <div class="detail-item">
        <label>返回URL：</label>
        <span class="url-text">{{ orderDetail.return_url || '-' }}</span>
      </div>

      <el-row :gutter="20">
        <el-col :span="12">
          <div class="detail-item">
            <label>客户端IP：</label>
            <span>{{ orderDetail.client_ip || '-' }}</span>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="detail-item">
            <label>用户代理：</label>
            <span class="user-agent">{{ orderDetail.user_agent || '-' }}</span>
          </div>
        </el-col>
      </el-row>

      <!-- 时间信息 -->
      <el-divider content-position="left">时间信息</el-divider>
      
      <el-row :gutter="20">
        <el-col :span="12">
          <div class="detail-item">
            <label>过期时间：</label>
            <span>{{ orderDetail.expire_time || '-' }}</span>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="detail-item">
            <label>支付时间：</label>
            <span>{{ orderDetail.paid_time || '-' }}</span>
          </div>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <div class="detail-item">
            <label>创建时间：</label>
            <span>{{ orderDetail.created_at }}</span>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="detail-item">
            <label>更新时间：</label>
            <span>{{ orderDetail.updated_at }}</span>
          </div>
        </el-col>
      </el-row>


      <!-- 订单流转日志 -->
      <el-divider content-position="left">订单流转日志</el-divider>
      
      <div v-loading="logsLoading" element-loading-text="加载日志中...">
        <el-table :data="orderLogs" border stripe max-height="400">
          <el-table-column prop="action" label="操作动作" width="120" align="center"/>
          <el-table-column prop="description" label="描述" min-width="200" show-overflow-tooltip/>
          <el-table-column prop="status" label="状态" width="100" align="center">
            <template #default="{ row }">
              <el-tag :type="getStatusTagType(row.status) as any" size="small">
                {{ getStatusText(row.status) }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="operator_type" label="操作者类型" width="100" align="center">
            <template #default="{ row }">
              {{ getOperatorTypeText(row.operator_type) }}
            </template>
          </el-table-column>
          <el-table-column prop="operator_name" label="操作者" width="120" align="center"/>
          <el-table-column prop="ip_address" label="IP地址" width="120" align="center"/>
          <el-table-column prop="created_at" label="操作时间" width="160" align="center"/>
        </el-table>
        
        <div v-if="orderLogs.length === 0" class="empty-logs">
          <el-empty description="暂无流转日志" :image-size="80"/>
        </div>
      </div>
    </div>
  </el-drawer>
</template>

